// Di dalam file yang mengandung tombol HTML
import { services } from "../templates/template-creator";
// import emailjs from 'emailjs-com';


const Service = {
  async render() {
    return services();
  },

  async afterRender() {
    function sendMail() {
//   emailjs.init("ErIGpjIxGjaFXFaQh"); // Ganti dengan user ID Anda yang sesuai

  // Dapatkan nilai dari input fields
  const name = document.getElementById("name").value;
  const email = document.getElementById("email").value;
  const message = document.getElementById("message").value;

  // Kirim email menggunakan EmailJS
  const templateParams = {
    name: name,
    email: email,
    message: message,
  };

  // ID service dan template dari EmailJS
  const serviceID = 'service_ke34k9k'; // Ganti dengan service ID Anda
  const templateID = 'template_25jrm9i'; // Ganti dengan template ID Anda

  // Kirim email
  emailjs.send(serviceID, templateID, templateParams)
    .then(function(response) {
      console.log('Email sent successfully:', response);
      alert('Email sent successfully!');
    })
    .catch(function(error) {
      console.error('Email send failed:', error);
      alert('Email send failed. Please try again later.');
    });

  // Kosongkan input fields
  document.getElementById("name").value = "";
  document.getElementById("email").value = "";
  document.getElementById("message").value = "";
}
    // Anda dapat memanggil sendMail di sini jika perlu
     // Dapatkan referensi ke tombol "Submit"
    const submitButton = document.querySelector('button[type="button"]');

    // Tambahkan pendengar acara untuk tombol "Submit"
    submitButton.addEventListener('click', function() {
      sendMail();
    });
  },
};

export default Service;
