___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --primary-color: #2a4f82;
    --bg: #ebe8e1;
    --text-color1: #0a0a28;
    --text-color2: #666680;
}

body {
    font-family: "nunito", sans-serif;
    background-color: var(--bg);
}


/* Navbar */

.navbar {
    background-color: var(--bg);
}

.nav-item {
    margin: 0 40px 0 0;
    color: var(--text-color1);
    font-weight: 600;
}

.btn {
    background-color: var(--primary-color);
    border-radius: 30px;
    color: var(--bg) !important;
    width: 140px;
}

.btn:hover {
    background-color: #112644 !important;
}


/* Jumbotron */

.jumbotron {
    margin-top: 200px;
}

.title {
    font-weight: bold;
}

.button-hero {
    margin-top: 20px;
}


/* Category */

.category {
    margin-top: 250px;
}

.category h4,
.team h4 {
    text-align: center;
    font-size: 24px;
    line-height: 43.65px;
    padding-top: 0.5rem;
    color: var(--text-color1);
}

.circle {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    background-color: #f1f1f1;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
}

.circle img {
    max-width: 100%;
    max-height: 100%;
    border-radius: 50%;
}

.circle h4 {
    text-align: center;
    color: var(--text-color2);
}


/* About */

.about {
    margin-top: 300px;
}

.about-img {
    width: 450px;
}

.area-title {
    font-weight: bold;
    color: var(--text-color1);
    margin-top: 15px;
    margin-bottom: 20px;
}

.info {
    font-size: 24px;
    color: var(--text-color1);
}

.about-info {
    text-align: justify;
}

.content-about {
    margin-top: 200px;
}

.content-aboutImg {
    width: 300px;
}

.about-desc,
.desricption-area p {
    text-align: justify;
    color: var(--text-color1);
    font-size: 18px;
    margin-top: 30px;
}

hr {
    margin-top: 25px;
    margin-bottom: 25px;
}


/* interest content */

.interest-content {
    margin-top: 300px;
}

.interest-img {
    border-radius: 30px;
}

.interest-container {
    margin-bottom: 300px;
}


/* List Article */

.listArticle {
    display: flex;
    flex-wrap: wrap;
    gap: 16px;
    justify-content: center;
}

.item-title {
    font-weight: bold;
}

.card-list {
    background-color: var(--bg);
    border-radius: 30px;
    border: none;
    box-shadow: 0px 5px 15px rgba(1, 1, 3, 0.1);
    margin-bottom: 30px;
    padding-bottom: 20px;
}

.list-img {
    border-radius: 30px 30px 0 0;
   height: 250px;
}

.explore-article {
    margin-top: 100px;
}


/* Detail Article */

.detailContent {
    margin-top: 200px;
}

.detail-img {
    border-radius: 30px;
    width: 50%;

}


/* Team */

.team {
    margin-top: 200px;
}

.team-title {
    margin-bottom: 100px;
}


/* Footer */

footer {
    margin-top: 150px;
    background-color: var(--primary-color);
    padding-top: 60px;
}

.contact-link,
.sosmed-link,
.copyright,
.contact {
    color: var(--bg);
}
.card-services{
    margin-top: 200px;
}`, "",{"version":3,"sources":["webpack://./src/styles/style.css"],"names":[],"mappings":"AAAA;IACI,wBAAwB;IACxB,aAAa;IACb,sBAAsB;IACtB,sBAAsB;AAC1B;;AAEA;IACI,iCAAiC;IACjC,2BAA2B;AAC/B;;;AAGA,WAAW;;AAEX;IACI,2BAA2B;AAC/B;;AAEA;IACI,kBAAkB;IAClB,yBAAyB;IACzB,gBAAgB;AACpB;;AAEA;IACI,sCAAsC;IACtC,mBAAmB;IACnB,2BAA2B;IAC3B,YAAY;AAChB;;AAEA;IACI,oCAAoC;AACxC;;;AAGA,cAAc;;AAEd;IACI,iBAAiB;AACrB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,gBAAgB;AACpB;;;AAGA,aAAa;;AAEb;IACI,iBAAiB;AACrB;;AAEA;;IAEI,kBAAkB;IAClB,eAAe;IACf,oBAAoB;IACpB,mBAAmB;IACnB,yBAAyB;AAC7B;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,kBAAkB;IAClB,yBAAyB;IACzB,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,cAAc;AAClB;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,kBAAkB;AACtB;;AAEA;IACI,kBAAkB;IAClB,yBAAyB;AAC7B;;;AAGA,UAAU;;AAEV;IACI,iBAAiB;AACrB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,iBAAiB;IACjB,yBAAyB;IACzB,gBAAgB;IAChB,mBAAmB;AACvB;;AAEA;IACI,eAAe;IACf,yBAAyB;AAC7B;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,YAAY;AAChB;;AAEA;;IAEI,mBAAmB;IACnB,yBAAyB;IACzB,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,gBAAgB;IAChB,mBAAmB;AACvB;;;AAGA,qBAAqB;;AAErB;IACI,iBAAiB;AACrB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,oBAAoB;AACxB;;;AAGA,iBAAiB;;AAEjB;IACI,aAAa;IACb,eAAe;IACf,SAAS;IACT,uBAAuB;AAC3B;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,2BAA2B;IAC3B,mBAAmB;IACnB,YAAY;IACZ,2CAA2C;IAC3C,mBAAmB;IACnB,oBAAoB;AACxB;;AAEA;IACI,4BAA4B;GAC7B,aAAa;AAChB;;AAEA;IACI,iBAAiB;AACrB;;;AAGA,mBAAmB;;AAEnB;IACI,iBAAiB;AACrB;;AAEA;IACI,mBAAmB;IACnB,UAAU;;AAEd;;;AAGA,SAAS;;AAET;IACI,iBAAiB;AACrB;;AAEA;IACI,oBAAoB;AACxB;;;AAGA,WAAW;;AAEX;IACI,iBAAiB;IACjB,sCAAsC;IACtC,iBAAiB;AACrB;;AAEA;;;;IAII,gBAAgB;AACpB;AACA;IACI,iBAAiB;AACrB","sourcesContent":[":root {\r\n    --primary-color: #2a4f82;\r\n    --bg: #ebe8e1;\r\n    --text-color1: #0a0a28;\r\n    --text-color2: #666680;\r\n}\r\n\r\nbody {\r\n    font-family: \"nunito\", sans-serif;\r\n    background-color: var(--bg);\r\n}\r\n\r\n\r\n/* Navbar */\r\n\r\n.navbar {\r\n    background-color: var(--bg);\r\n}\r\n\r\n.nav-item {\r\n    margin: 0 